import React, {Component} from 'react';
class Home extends Component {
    render() {
	    return (
		<div className="row ">
	        <div className="medium-12 columns">
                <div>
                        <header>
                            <nav id="logo">
                                <img src="./Images/Logo.png"/>
                            </nav>
                            <nav id="nav1">
                                <ul>
                                    <li><a href="#firstdiv">Home</a></li>
                                    <li><a href="#secondback">About Us</a></li>
                                    <li><a href="https://nxs0682.uta.cloud/">Blog</a></li>
                                    <li><a href="#" onClick={window['visitorform']}>Visitors</a></li>
                                    <li><a href="#footer">Contact</a></li>
                                </ul>
                            </nav>
                        </header>

                        <div id="firstdiv">
                            <h2>Your Safe Space</h2>
                            <h1>Lunamar Housing</h1>
                            <img id="firstback" src="./Images/background dark.jpg"/>
                            <a href="#" id="login" onClick={window['loginform']}>Login</a>
                            <a href="#" id="signup" onClick={window['signupform']}>Signup</a>
                            <img id="mix" src="./Images/mix.png"/>
                        </div>
                        <div id="aboutdiv">
                            <img id="secondback" src="./Images/table.png"/>
                            <ul>
                                <li>Lunamar management is the leading online website which offers services to all residents.</li>
                                <li>It is one stop step to all residents to avail their servies and manage them.</li>
                                <li>Our focus is to manage buildings, services , gardens and surroundings.</li>
                                <li>Our community includes more than 100 acres of green space, parks and premier amenities.</li>
                                <li>Resident can also talk to management or admin to resolve there issues.</li>
                                <li>The Visitors can fill up the form and enter the property without any extra efforts.</li>
                                <li>Please feel free contact us and we will get back to you in no time.</li>
                            </ul>
                        </div>
                        <div id="login-form">
                            <form>
                                <a style={{float:"right"}} href="#" className="fa fa-times" onClick={window['loginhide']}></a>
                                <h1>Login</h1><br/>
                                <label>Login As:</label>
                                <select id="login-option">
                                    <option>Resident</option>
                                    <option>Manager</option>
                                    <option>Visitor</option>
                                </select><br/><br/><br/>
                                <label htmlFor="email"><b>Username</b></label><br/><br/>
                                <input type="text" name="email" required/><br/><br/><br/>

                                <label htmlFor="password"><b>Password</b></label><br/><br/>
                                <input type="password" name="password" required/><br/><br/><br/>
                                
                                <button type="button" className="subbtn" onClick={window['landingpage']}>Login</button>
                                <a id="forgotp" href="#"  onClick={window['forgotpassform']}>Forgot Password?</a>
                            </form>
                        </div>
                        <div id="forgot-pass-form">
                            <form>
                                <a style={{float:"right"}} href="#" className="fa fa-times" onClick={window['loginhide']}></a>
                                <h1>Forgot Password</h1><br/>
                                <label for="email"><b>Email</b></label><br/><br/>
                                <input type="text" name="email" required/><br/><br/><br/>
                                
                                <button type="button" class="subbtn">Send</button>
                            </form>
                        </div>
                        <div id="signupform">
                            <form action="#">
                                <a style={{float:"right"}} href="#" className="fa fa-times" onClick={window['loginhide']}></a>
                                <h1>Sign Up</h1><br/>
                                <div style={{textAlign:"left", float:"left"}}>
                                    <label htmlFor="firstname">FirstName</label>
                                    <label id="firstname-status" style={{color:"red"}}>*</label><br/>
                                    <input type="text" id="firstname" name="firstname" onKeyUp={window['firstnamecheck']} required/>
                                    <br/><br/><br/>
                                    <label htmlFor="lastname">LastName</label>
                                    <label id="lastname-status" style={{color:"red"}}>*</label><br/>
                                    <input type="text" id="lastname" name="lastname" onKeyUp={window['lastnamecheck']} required/>
                                    <br/><br/><br/>
                                    <label htmlFor="address">Address</label><br/>
                                    <input type="text" name="address"/><br/><br/><br/>
                                    <label htmlFor="phone">Phone</label><br/>
                                    <input type="text" name="phone"/><br/><br/><br/>
                                </div>
                                <div style={{textAlign:"left", marginLeft:"400px"}}>
                                    <label>Sign up As:</label><br/>
                                    <select id="signup-option">
                                        <option>Resident</option>
                                        <option>Manager</option>
                                        <option>Visitor</option>
                                    </select><br/><br/><br/>
                                    <label htmlFor="email">Email</label>
                                    <label id="email-status" style={{color:"red"}}>*</label><br/>
                                    <input id="email" type="email" name="email" onKeyUp={window['emailcheck']} required/>
                                    <br/><br/><br/>
                                    <label htmlFor="password">Password</label>
                                    <label id="password-status" style={{color:"red", marginLeft:"10px"}}>*</label><br/>
                                    <input type="password" id="password" name="password" onKeyUp={window['passcheck']} required/>
                                    <br/><br/><br/>
                                    <label htmlFor="cpassword">Confirm Password</label>
                                    <label id="cpassword-status" style={{color:"red", marginLeft:"10px"}}>*</label><br/>
                                    <input type="password" id="cpassword" name="cpassword" onKeyUp={window['cpasscheck']} required/>
                                    <br/><br/><br/>
                                </div>
                                <button name="register" id="signupregister" type="button" className="ssubbtn" onClick={window['sendemail']}>Register</button><br/>
                                <label id="ssuccess"></label>
                            </form>
                        </div>

                        <div id="visitor-form">
                            <form action="#">
                                <a style={{float:"right"}} href="#" className="fa fa-times" onClick={window['loginhide']}></a>
                                <h1>Visitor Form</h1><br/>
                                <div style={{textAlign:"left", float:"left"}}>
                                    <label htmlFor="name"><b>Name</b></label><br/><br/>
                                    <input type="text" name="name"/><br/><br/><br/>

                                    <label htmlFor="aptno"><b>Apartment Number</b></label><br/><br/>
                                    <input type="text" name="aptno"/><br/><br/><br/>

                                    <label htmlFor="rname"><b>Resident Name</b></label><br/><br/>
                                    <input type="text" name="rname"/><br/><br/><br/>
                                </div>
                                <div style={{textAlign:"left", marginLeft:"400px"}}>
                                    <label htmlFor="vdate"><b>Date of visit</b></label><br/><br/>
                                    <input type="text" name="vdate" placeholder="mm-dd-yyyy"/><br/><br/><br/>

                                    <label htmlFor="email"><b>Email</b></label><br/><br/>
                                    <input type="text" name="email"/><br/><br/><br/>

                                    <label htmlFor="phone"><b>Contact</b></label><br/><br/>
                                    <input type="text" name="phone"/><br/><br/><br/>
                                </div> 
                                <button type="submit" className="subbtn">Submit</button>
                            </form>
                        </div>

                        <div id="admin-login-form">
                            <form action="/admin">
                                <a style={{float:"right"}} href="#" className="fa fa-times" onClick={window['loginhide']}></a>
                                <h1>Admin Login</h1><br/>
                                <label htmlFor="email"><b>Admin ID</b></label><br/><br/>
                                <input type="text" name="email" required/><br/><br/><br/>

                                <label htmlFor="password"><b>Password</b></label><br/><br/>
                                <input type="password" name="password" required/><br/><br/><br/>
                                
                                <button type="submit" className="subbtn">Login</button>
                                <a id="forgotp" href="#">Forgot Password?</a>
                            </form>
                        </div>

                        <footer id="footer">
                            <h2>Contact Us</h2>
                            <form>
                                <input type="text" id="contactfirstname" placeholder="Firstname"/>
                                <input type="text" placeholder="Lastname"/><br/><br/><br/>
                                <input type="text" placeholder="Contact"/>
                                <input type="text" id="contactemail" placeholder="Email"/><br/><br/><br/>
                                <textarea id="contactquery" placeholder="Query" rows="4" cols="50"></textarea><br/><br/><br/>
                                <button type="button" onClick={window['sendMail']} className="contactbtn">Submit</button><br/>
                                <label id="success"></label>
                            </form><br/><br/><br/>
                            <img src="./Images/Logo.png"/>
                            <h3>Place where your dream home awaits you</h3>
                            <a href="#" className="fa fa-facebook"></a>
                            <a href="#" className="fa fa-twitter"></a>
                            <a href="#" className="fa fa-instagram"></a>
                            <a href="#" className="fa fa-google"></a>
                            <a href="#" className="fa fa-linkedin"></a><br/>
                            <a style={{textDecoration:"none", color:"blue"}} href="#" onClick={window['adminloginform']}>Admin Login</a>
                        </footer>
                    </div>
                </div>
        </div>  
	     );
    }
}
export default Home;